import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import Container from "@mui/material/Container";
import MKBox from "components/MKBox";
import "../css/CTableTitle.css";
function BoardPages() {
  const { id } = useParams();
  const [post, setPost] = useState(null);

  //   .  API   .
  const samplePosts = [
    {
      id: "1",
      title: "  ",
      content: "   .",
      tags: ["", "primary", "error"],
      date: "2023-09-17",
      author: "",
    },
    {
      id: "2",
      title: "  ",
      content: "   .",
      tags: ["", "primary", "error"],
      date: "2023-09-17",
      author: "",
    },
    {
      id: "3",
      title: "  ",
      content: "   .",
      tags: ["", "primary", "error"],
      date: "2023-09-17",
      author: "",
    },
    {
      id: "4",
      title: "  ",
      content: "   .",
      tags: ["", "primary", "error"],
      date: "2023-09-17",
      author: "",
    },
  ];

  useEffect(() => {
    // postId   
    const currentPost = samplePosts.find((p) => parseInt(p.id) === parseInt(id));
    setPost(currentPost);
  }, [id]);
  if (!post) return <div>   .</div>;

  return (
    <MKBox component="section" py={10}>
      <Container>
        <h1>{post.title}</h1>
        <p>{post.content}</p>
      </Container>
    </MKBox>
  );
}
export default BoardPages;
